.axis {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

.axis text {
  font: 11px sans-serif;
  fill: #000;
  stroke: none;
  font-weight: bold;
}

.axis line {
  stroke: #ccc;
}

.axis path {
  stroke: #ccc;
}

.line {
  padding: 10px;
}

.line:hover {
  stroke: #e55;
}

.arc path:hover {
  fill: #e55;
}

.arc text {
  font: 11px sans-serif;
  fill: #000;
  stroke: none;
  font-weight: bold;
}

.tooltip {
  padding: 3px;
  border: 2px solid;
  border-radius: 4px;
  background-color: #eee;
  opacity: 0.6;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
  color: black;
}

.brush {
  .extent {
    stroke: #000;
    fill-opacity: 0.125;
    shape-rendering: crispEdges;
  }

  .background {
    fill: #ddd;
  }

  .x.axis.label {
    font-size: large;
  }
}

.nofloat {
  float: 'none';
}

.rightY {
  position: relative;
  left: 0px;
}

.sentimentChart {
  height: 500px;
}

.title {
  text-align: left;
  font-weight: bold;
  color: black;
  &.brush {
    font-size: small;
  }
}
